.contactPage {
    .contactPage__row {
        display: flex;
        margin-top: 4rem;
    }

    .contactPage__col {

    }

    .contactPage__col--info {
        width: 40%;
    }

    .contactPage__col--map {
        width: 60%;
    }

    .contactPage__companyName {
        font-size: 16px;
        color: #000;
    }

    .contactPage__contacts {
        .contactPage__contact {
            display: block;
        }
    }

    .contactPage__map {
        width: 100%;
        height: 450px;
    }
}